<template>
  <div class="appli">
    <!-- 切换形象 -->
    <KeepAlive>
      <PinchFaceVue v-if="pinchFaceVisible" @next="receivePinchFace" />
    </KeepAlive>
    <!-- 装扮形象 -->
    <KeepAlive>
      <DressUpVue v-if="dressUpVidible" @prev="receiveDressUpReturn" />
    </KeepAlive>
  </div>
</template>
<script>
import PinchFaceVue from './PinchFace.vue'
import DressUpVue from './DressUp.vue'

export default {
  components: {
    PinchFaceVue,
    DressUpVue
  },
  data() {
    return {
      pinchFaceVisible: false,
      dressUpVidible: true
    }
  },
  methods: {
    // 接收切换形象下一步
    receivePinchFace(val) { //dressUpVidible
      this.pinchFaceVisible = false;
      this[val] = true;
    },
    // 接收装扮形象返回上一步
    receiveDressUpReturn(val) {
      this[val] = true;
      this.dressUpVidible = false
    }
  }
}
</script>
<style lang='scss' scoped>
 .appli {
   width: 100%;
   height: 100%;
 }
 </style>