<template>
  <div class="pinch">
    <p class="pinch-title">请选择您的虚拟头像，一起在元宇宙里遨游吧！</p>
    <div style="width: 400px;height: 650px;margin: 0 auto; position: relative">
      <div class="arrow arrow-left" @click="arrowClick('left')"><i class="el-icon-arrow-left"></i></div>
      <el-carousel height="650px" indicator-position="none" :autoplay="false" arrow="never"  ref="cardShow">
        <el-carousel-item v-for="item in 4" :key="item">
          <div class="person-title">
            <span>诸神之王 宙斯</span>
            <div>
              <el-image fit="fill" :src="rotateUrl"></el-image>
            </div>
          </div>
        </el-carousel-item>
      </el-carousel>
      <div class="arrow arrow-right" @click="arrowClick('right')"><i class="el-icon-arrow-right"></i></div>
    </div>
    <el-row type="flex" justify="center">
      <el-button type="primary" style="padding: 8px 100px;" round @click="nextFn">下一步</el-button>
    </el-row>
  </div>
</template>
<script>
export default {
  data() {
    return {
      rotateUrl: require('@/assets/images/rotate.png')
    }
  },
  methods: {
    arrowClick(val) {
      if(val === 'right') {
        this.$refs.cardShow.next()
      } else {
        this.$refs.cardShow.prev()
      }
    },
    nextFn() {
      this.$emit('next', 'dressUpVidible')
    }
  }
}
</script>
<style lang='scss' scoped>
 .pinch {
   &-title {
     padding-top: 80px;
     color: #000;
     font-size: 20px;
     text-align: center;
     margin-bottom: 50px;
     font-weight: 500;
   }
   .arrow {
     position: absolute;
     top: 275px;
     cursor: pointer;
     i {
       font-size: 55px;
       font-weight: 600;
       color: #32cdcd;
     }
   }
   .arrow-left {
     left: -100px;
   }
   .arrow-right {
     right: -100px;
   }
   .person-title {
     text-align: center;
     >span:nth-of-type(1) {
       display: inline-block;
       padding: 4px 16px;
       background-color: #FFBE17;
       border-radius: 24px;
       color: #fff;
     }
     >div:nth-of-type(1) {
       width: 44px;
       height: 44px;
       margin: 0 auto;
     }
   }
 }
 </style>