<template>
  <div class="dress">
    <el-row type="flex">
      <div class="dress-left-bar"></div>
      <el-row class="dress-left-card" type="flex">
        <el-col :class="{'active': curCard ==='face'}" @click.native="cardClick('face')">
          <div><span>脸</span><span>型</span></div>
        </el-col>
        <el-col :class="{'active': curCard ==='hair'}" @click.native="cardClick('hair')">
          <div><span>发</span><span>型</span></div>
        </el-col>
        <el-col :class="{'active': curCard ==='eye'}" @click.native="cardClick('eye')">
          <div><span>眼</span><span>睛</span></div>
        </el-col>
        <el-col :class="{'active': curCard ==='eyebrow'}" @click.native="cardClick('eyebrow')">
          <div><span>眉</span><span>毛</span></div>
        </el-col>
        <el-col :class="{'active': curCard ==='nose'}" @click.native="cardClick('nose')">
          <div><span>鼻</span><span>子</span></div>
        </el-col>
        <el-col :class="{'active': curCard ==='clothes'}" @click.native="cardClick('clothes')">
          <div><span>服</span><span>饰</span></div>
        </el-col>
      </el-row>
      <div class="dress-content">
        <p class="dress-content-title">请装扮您的虚拟头像，成为全场最靓的崽吧~</p>
          <el-row type="flex" class="dress-content-card">
            <!-- 选择装扮 -->
            <el-col :span="15" style="padding:0 10px;">
              <el-scrollbar style="height: 100%">
                <el-row type="flex" justify="start" style="flex-wrap: wrap;">
                  <template v-for="(item, index) in faceList">
                    <div  :class="{'dress-content-card-item':true,'dress-content-card-active': chooseFace===index}" @click="chooseFn(index)">
                      <div>
                        <div v-if="item.type==='normal'" style="position: relative;">
                          <el-image style="width:40px;height:40px;margin: 30px;" fit="fill" :src="item.url"></el-image>
                          <span style="color: #cecdd0;position:absolute;font-size: 14px;top: 40px;left: 37px;">默认</span>
                        </div>
                        <el-image v-else style="width:100px;height:100px;" fit="fill" :src="item.url"></el-image>
                      </div>
                    </div>
                  </template>
                </el-row>
              </el-scrollbar>
            </el-col>
            <!-- 人物图像 -->
            <el-col :span="9">
              <el-row type="flex" justify="center">
                <el-button type="primary" style="border-radius:8px;" size="mini">全身</el-button>
                <el-button type="primary" :plain="true" style="border-radius:8px;" size="mini">半身</el-button>
              </el-row>
              <el-row type="flex" justify="center" style="margin-top: 10px;">
                  <el-image style="width: 34px;height: 34px;" fit="fill" :src="rotateUrl"></el-image>
              </el-row>
              <div class="person"></div>
              <el-row type="flex" justify="center" style="margin-top: 70px;">
                <el-button type="primary" size="mini" plain round style="padding: 8px 35px" @click="prevFn">返回上一步</el-button>
                <el-button type="primary" size="mini" round style="padding: 8px 35px" @click="saveFn">保存形象</el-button>
              </el-row>
            </el-col>
          </el-row>
      </div>
    </el-row>
  </div>
</template>
<script>
export default {
  data() {
    return {
      curCard: 'face',
      faceList: [{
        type: 'normal',
        url: require('@/assets/images/disabled.png')
      },{
        url: require('@/assets/images/rotate.png')
      },{},{},{},{},{},{},{},{},{},{},{},{},{},{},{},{},{},{},{},{},{},{},{}],
      chooseFace: 3,
      rotateUrl: require('@/assets/images/rotate.png')
    }
  },
  methods: {
    // 卡片点击
    cardClick(val) {
      this.curCard = val;
    },
    chooseFn(index) {
      this.chooseFace = index;
    },
    // 返回上一步
    prevFn() {
      this.$emit('prev','pinchFaceVisible')
    },
    // 保存形象
    saveFn() {
      this.$router.push({
        path: '/appli/enter'
      })
    }
  }
}
</script>
<style lang='scss' scoped>
.dress {
   width: 100%;
   height: 100%;
   >div {
     width: 100%;
     height: 100%;
   }
   &-left-bar {
     width: 72px;
     height: 100%;
     background-color: #48464B;
   }
   &-left-card {
     width: 28px;
     height: 100%;
     flex-direction: column;
     >div.active {
       background-color: #32cdcd;;
     }
     >div{
       flex: auto;
       background-color: #84818A;
       border-radius: 8px;
       color: #fff;
       text-align: center;
       align-items: center;
       div {
        position: relative;
        top:50%;
        transform:translateY(-50%);
        cursor: pointer;
       }
       span {
        display: inline-block;
        transform: rotate(90deg);
       }
     }
   }
   &-content {
     width: calc(100% - 100px);
     height: 100%;
     &-title {
      padding-top: 80px;
      color: #000;
      font-size: 20px;
      text-align: center;
      margin-bottom: 50px;
      font-weight: 500;
     }
     &-card {
       height: 640px;
       padding: 0 90px;
       &-item {
        width: 140px;
        height: 140px;
        border-radius: 8px;
        background: #f0f0f033;
        box-shadow: 2px 4px 4px #0000000d;
        margin: 9px;
        padding: 20px;
        cursor: pointer;
        >div {
          width: 100px;
          height: 100px;
          background-color: #f8f8f8;
        }
       }
       &-active {
        border: 2px solid #32cdcd;
        background: #f4fbfb;
       }
     }
     .person {
       width: 285px;
       height: 420px;
       background: #ccc;
       margin: 0 auto;
       margin-top: 30px;
     }
   }
}
</style>